	module.exports = {"data":{"markdownRemark":{"html":"<p><code>hex(x)</code> is a built-in function in Python 3 to convert an integer number to a lowercase <a href='https://www.mathsisfun.com/hexadecimals.html' target='_blank' rel='nofollow'>hexadecimal</a> string prefixed with “0x”.</p>\n<h2>Argument</h2>\n<p>This function takes one argument, <code>x</code>, that should be of integer type.</p>\n<h2>Return</h2>\n<p>This function returns a lowercase hexadecimal string prefixed with “0x”.</p>\n<h2>Example</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>print(hex(16))    # prints  0x10\nprint(hex(-298))  # prints -0x12a\nprint(hex(543))   # prints  0x21f</code></pre>\n      </div>\n<p><img src=\"//forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=2\" title=\":rocket:\" alt=\":rocket:\" class=\"forum-image\"> <a href='https://repl.it/CV0S' target='_blank' rel='nofollow'>Run Code</a></p>\n<p><a href='https://docs.python.org/3/library/functions.html#hex' target='_blank' rel='nofollow'>Official Documentation</a></p>","fields":{"slug":"/python/hex-functions/"},"frontmatter":{"title":"Python Hex Function"}}},"pathContext":{"slug":"/python/hex-functions/"}}

